body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Roboto'
}

.App-header {
  background-color: #282c34;
  min-height: 120px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(22px + 2vmin);
  color: white;
  transition: all 0.3s ease-in-out;
  padding: 10px;
}

.caveat {
  font-size: 12px;
}

.Selection-panel {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
  padding: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.Selection-panel-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}


@media (max-width: 800px) {
  .Selection-panel-wrapper {
      -webkit-flex-direction: column;
              flex-direction: column; 
  }
}

.country-selector{
  max-width: 400px;
}

.no-adapter {
  color: #00e676;
}

.yes-adapter {
  color: #f44336;
}

.plug-wrapper {
    width: 350px;
    height: 350px;
    border: 2px solid #282c34;
    border-radius: 20px;
    margin-top: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
}

.plug-circle {
    width: 300px;
    height: 300px;
    border: 2px solid #282c34;
    border-radius: 50%;
    position: relative;
}

.socket-animation {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.plug-type-changer {
    font-size: 24px;
    color: #282c34;
    cursor: pointer;
}

.plug-type-changer.selected, .plug-type-changer.selected:hover{
    color: red;
}

.plug-type-changer:hover{
    color: #7b7e85;
}

.first-hole,
